* {
  box-sizing: border-box;
}

body {
  height: 100vh;
  background: #222 url("https://source.unsplash.com/1600x900/?landscape") 50% no-repeat;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-family: Open Sans, sans-serif;
  font-size: 100%;
  display: flex;
}

.card {
  color: #fff;
  width: 100%;
  max-width: 420px;
  background: #000000d0;
  border-radius: 30px;
  margin: 1em;
  padding: 2em;
}

input.search-bar {
  color: #fff;
  width: calc(100% - 100px);
  background: #7c7c7c2b;
  border: none;
  border-radius: 24px;
  outline: none;
  justify-content: center;
  align-items: center;
  padding: .4em 1em;
  font-family: inherit;
  font-size: 105%;
  display: flex;
}

.country-list {
  flex-flow: column wrap;
  justify-content: space-between;
  gap: 16px;
  font-size: 16px;
  list-style: none;
  display: flex;
}

.country-info {
  text-align: left;
  flex-direction: column;
  gap: 2px;
  font-size: 20px;
  display: flex;
}

